import React from 'react'

import './Hero.scss'

const Hero = () => {
  return (
    <div className='hero'>
      <img
        className='hero__image'
        src={require('./images/hero.jpg')}
        alt='Hero'
      />
      <div className='hero__description'>
        <p>!</p>
        <p className='hero__paragraph hero__paragraph-first'>
              -  Fashion Stylist  .
        </p>
        <p className='hero__paragraph'>
               .
           FASHION ACADEMY PSFA.
        </p>
      </div>
    </div>
  )
}

export default Hero
