import React from 'react'

import './PriceBlock.scss'

const PriceBlock = () => {
  return (
    <div className='price-block' id='pricelist'>
      <div className='price-block__info'>
        <h2 className='price-block__title'>-</h2>
        <span className='price-block__subtitle'>
          <span> </span>
        </span>
        <ul className='price-block__list'>
          <span>  </span>
          <span> 100 BYN/</span>
          <span>   / </span>
          <span> 150 BYN/ 1 </span>
          <span> </span>
          <span> 330 BYN</span>
          <span></span>
          <span> 330 BYN/ 2 </span>
          <span>     </span>
          <span> 500 BYN</span>
          <span>   </span>
          <span> 800 BYN</span>
          <span> </span>
          <span> 1500 BYN</span>
        </ul>
      </div>
      <img
        className='price-block__image'
        src={require('./images/price-picture.jpg')}
        alt='Price block'
      />
    </div>
  )
}

export default PriceBlock
